html,
body,
#root {
  height: 100%;
  margin: 0;
  background:  rgb(184 168 154);
}

svg text {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.annotationDescription {
  pointer-events: none;
  font-size: 14px;
  position: absolute;
  top: 20px;
  padding: 1em;
  width: 200px;
  text-color: rgba(17, 24, 39,  0.66);
  border-radius: 0.5em;
  display: block;
}

#annotationsPanel   {
  position: absolute;
  top: 120px;
  left: 4px;
  width: auto;
  height: auto;
}

#annotationsPanel ul {
  padding: 0;
  list-style-type: none;
}

.annotationButton {
  width: 160px;
  height: 70px;
  background-color: rgba(17, 24, 39,  0.66);
  text-align: left;
  padding-left: 20px;
  cursor: pointer;
  color: rgb(209, 213, 219);
  font-family: monospace;
}
